<template>
  <div class="wrap-layer">
    <div class="header">
      <van-image round width="40px" height="40px" :src="Img" @click="showDog" />
      <van-icon name="calendar-o" color="#fff" size="40" @click="showCalendar = true" />
      <van-calendar v-model:show="showCalendar" position="right" :show-confirm="false" :formatter="formatter" first-day-of-week="1"/>
    </div>
    <div class="mt30 my_font">
      <van-grid square column-num="4" :gutter="20">
        <van-grid-item v-for="(member,index) in members2" :key="index" icon="photo-o" @click="currentMember == member?showCalendar = true:''" :text="member" :class="{active:currentMember == member}">
          <svg-icon :name="icons2[index]"></svg-icon>
          <span>{{member}}</span>
        </van-grid-item>
      </van-grid>
    </div>
    <current-time :currentMember="currentMember"></current-time>
  </div>
</template>

<script>
import { Button, Icon } from 'vant'
import { Image as VanImage } from 'vant'
import { Calendar as VanCalendar } from 'vant'
import { Grid as VanGrid, GridItem as VanGridItem } from 'vant'
import { ImagePreview } from 'vant'
import CurrentTime from './time.vue'
import Img from '@/assets/imgs/logo_dog.jpg'

export default {
  components: {
    CurrentTime,
    vanButton: Button,
    vanIcon: Icon,
    VanImage,
    VanCalendar,
    VanGrid,
    VanGridItem,
  },
  data() {
    return {
      showCalendar: false,
      Img,
      members: ['阿龙', '小马', '阿音', '越越'],
      members2: ['阿龙', '小马', '阿音', '越越'],
      icons2: [
        'icon-touxiang2',
        'icon-touxiang-',
        'icon-touxiang',
        'icon-touxiang1',
      ],
      currentMember: '- -',
    }
  },
  created() {
    console.log(this.ImagePreview)
    //test
    // let r = this.getSomeTime(2021, 3, 17)
    // this.getCurrentMember(r)
    this.currentMember = this.getCurrentMember()
  },
  mounted() {},
  methods: {
    ImagePreview,
    showDog() {
      this.ImagePreview([this.Img])
    },
    getSomeTime(year, month, date, hour, minutes, second) {
      let r = new Date()
      if (year) r.setFullYear(year)
      if (month) r.setMonth(month - 1)
      if (date) r.setDate(date)
      if (minutes) r.setMinutes(minutes)
      if (second) r.setSeconds(second)

      return r
    },
    getCurrentMember(time) {
      let startTime = new Date(1610294400000)
      let targetTime = time || new Date()
      let passTime = targetTime - startTime

      let days_7_times = Math.floor(passTime / (7 * 24 * 3600 * 1000))
      let index = days_7_times % 4
      return this.members[index]
    },

    formatter(day) {
      if (day.date < new Date() - 24 * 3600 * 1000) {
        return day
      } else {
        day.topInfo = this.getCurrentMember(day.date)
      }
      return day
    },
  },
}
</script>

<style lang="scss" scoped>
.wrap-layer {
  // background: #3aafa9;
}
.header {
  background: #252839;
  padding: 0 10px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

::v-deep {
  .van-grid-item {
    border: none;
    .van-grid-item__content::after {
      border: none;
    }
    .van-grid-item__content--surround::after {
      border: none;
    }
    .van-grid-item__content {
      border-radius: 50%;
      color: #252839;
      border: #feffff;
      .icon {
        width: 40px;
        height: 40px;
        margin-right: 0px;
      }
    }

    .van-grid-item__text {
      color: #17252a;
      font-weight: bold;
    }
  }

  .van-grid-item.active {
    border: none;
    .van-grid-item__content::after {
      border: none;
    }
    .van-grid-item__content--surround::after {
      border: none;
    }
    .van-grid-item__content {
      border-radius: 50%;
      color: #B5B5B7;
      font-weight: bolder;
      background: #48528b;
    }
    .van-grid-item__text {
      color: #17252a;
    }
  }
}
</style>