<template>
  <span>
    <svg class="icon" aria-hidden="true">
      <use :xlink:href="iconName"></use>
    </svg>
  </span>
</template>

<script>
export default {
  props: {
    name: {
      type: String,
      default: '',
    },
  },
  computed: {
    iconName() {
      return `#${this.name}`
    },
  },
  data() {
    return {}
  },
  mounted() {},
}
</script>
<style lang="scss" scoped>
.icon {
  display: inline-block;
  font-size: 0;
  vertical-align: middle;
  margin-right: 5px;
  width: 20px;
  height: 20px;
  fill: currentColor;
  overflow: hidden;
}
</style>